.header {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap; }

.header__info {
  width: 40%;
  height: 100%; }

.header__info span {
  font-weight: bold; }

.header__info img {
  width: 100%; }

.header__info p {
